export default{
	data(){
		return {
			guessLike : [],
		}
	},
	created() {
		let that = this;
		uni.request({  //请求推荐商品列表
			url:'http://ceshi3.dishait.cn/api/goods/hotlist',
			success: (res) => {
				that.guessLike = res.data.data;
			}
		})
	}
}
